import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import FormSelectionPage from "../pages/FormSelectionPage/FormSelectionPage";
import MappingSelectionPage from "../pages/MappingSelectionPage/MappingSelectionPage";
import NotFoundPage from "../pages/NotFoundPage/NotFoundPage";
import { g } from "../../globalState/globalState";
import { setNextPage } from "../redux-toolkit/slice/TaskpaneSlice";
import axios from "axios";
import { DTX_BASE_URL } from "../../config";

function App() {
  const [taskPanePage, setTaskPanePage] = useState({ taskPanePage: null });
  const { nextPage } = useSelector((state) => state.taskpane);
  const dispatch = useDispatch();
  useEffect(() => {
    setTaskPanePage(localStorage.getItem("taskPanePage"));
  }, [nextPage]);
  useEffect(() => {
    let page = localStorage.getItem("taskPanePage");
    if (page !== taskPanePage) {
      dispatch(setNextPage(page));
    }
  });
  let obj = {
    configureForm: <FormSelectionPage />,
    mappingPage: <MappingSelectionPage />,
    notFoundPage: <FormSelectionPage />,
  };

  if (obj[taskPanePage]) {
    return obj[taskPanePage];
  } else {
    return obj["notFoundPage"];
  }
}
export default App;
