/* eslint-disable react/jsx-key */
import React, { useState } from "react";
import BeaconHeader from "../../commonComponents/BeaconHeader/BeaconHeader";
import "./MappingSelectionPage.css";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { setMMmappingData, setMMmappingData2, setUploadLoader } from "../../redux-toolkit/slice/TaskpaneSlice";
import { BeatLoader } from "react-spinners";

function MappingSelectionPage() {
  const dispatch = useDispatch();
  const { typeOfData, typeOfMapping, MMmappingData, uploadLoader } = useSelector((state) => state.taskpane);
  const [rangeType, setrangeType] = useState({});
  const [rangeFor, setRangeFor] = useState({});
  const [lastRowNumber, setLastRowNumber] = useState(null);
  // const [MMmappingData, setMMmappingData] = useState({});
  const [intersection, setIntersection] = useState(false);
  // const [MMexcelData, setMMexcelData] = useState({});
  const [mustHaveField, setMustHaveField] = useState(false);

  const field = ["Column", "Selected Region", "Row"].map((option) => ({
    value: option.toLowerCase(),
    label: option,
  }));
  const fieldFor = [
    { label: "Custom Material Id* ", value: "custom_material_id" },
    { label: "ERP Material Id ", value: "erp_material_id" },
    { label: "Gross Weight", value: "gross_weight" },
    { label: "Inventory", value: "inventory" },
    { label: "Lead Time", value: "lead_time" },
    { label: "Material Description ", value: "material_description" },
    { label: "Material Group", value: "material_group" },
    { label: "Material Name* ", value: "material_name" },
    { label: "Material Product Code ", value: "material_product_code" },
    { label: "Material Type ", value: "material_type" },
    { label: "Others", value: "others" },
    { label: "Parent Id ", value: "parent_id" },
    { label: "PLM Material Id* ", value: "plm_material_id" },
    { label: "PLM Record Id", value: "plm_record_id" },
    { label: "Product Code ", value: "product_code" },
    { label: "Quantity", value: "quantity" },
    { label: "Status", value: "status" },
    { label: "Stogae", value: "storage_loc" },
    { label: "Unit Cost", value: "unit_cost" },
    { label: "Unit of Material* ", value: "unit_of_material" },
    { label: "Unit Sale Price", value: "unit_sale_price" },
    { label: "Weight Unit", value: "weight_unit" },
  ];
  const customStyles = {
    control: (base, state) => ({
      ...base,
      borderColor: state.isFocused ? "#383332" : "##383332",
      boxShadow: state.isFocused ? null : null,
      "&:hover": { borderColor: state.isFocused ? null : null },
      width: 250,
      height: 30,
    }),
    option: (base, state) => ({
      ...base,
      backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
      color: state.isSelected ? "#383332" : "#383332",
      "&:hover": { backgroundColor: "#F8F4F0" },
    }),
  };
  const selectRangeFor = (rangeFor) => {
    setRangeFor(rangeFor);
  };
  const selectRangeType = (rangeType) => {
    setrangeType(rangeType);
  };
  const reserveSelectedSpace = async () => {
    try {
      rangeType &&
        rangeFor &&
        (await Excel.run(async (context) => {
          const range = context.workbook.getSelectedRange();
          range.load("address");
          await context.sync();
          if (rangeType.label === "Column") {
            let rangeAddress = range.address.split("!");
            let selectedAddress = String(rangeAddress[1]).substring(0, 1);
            // if (Object?.values(MMmappingData)?.filter((element) => element?.address === selectedAddress)?.length > 0) {
            //   setIntersection(true);
            //   setTimeout(() => {
            //     setIntersection(false);
            //   }, 2000);
            // } else {
            let address = String(selectedAddress) + ":" + String(selectedAddress);
            dispatch(setMMmappingData({ name: rangeFor.label, value: rangeFor.value, address: selectedAddress }));
            // context.workbook.worksheets.getActiveWorksheet().getRange(address).format.fill.color = "#ffe2b3"; // fill back goround color to the selected region
            return context.sync();
            // }
          }
        }));
    } catch (error) {
      console.log("Error:", error);
    }
  };

  const uploadData = async () => {
    await Excel.run(async function (context) {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const lastRow = sheet.getUsedRange().load("rowCount");
      await context.sync();
      console.log(MMmappingData);
      setLastRowNumber(parseInt(lastRow.rowCount));
      if (
        !MMmappingData.hasOwnProperty(
          "custom_material_id" && "material_name" && "plm_material_id" && "unit_of_material"
        )
      ) {
        setMustHaveField(true);
        setTimeout(() => {
          setMustHaveField(false);
        }, 2000);
      } else {
        const tenantToken = localStorage.getItem("tenantToken");
        console.log(lastRowNumber, "lastRowNumber", parseInt(lastRow.rowCount));
        if (lastRowNumber) {
          dispatch(setUploadLoader(true));
        }
        for (let i = 2; i < parseInt(lastRow.rowCount); i++) {
          await uploadDataToDtx(i, tenantToken);
          if (i === lastRowNumber - 1) {
            dispatch(setUploadLoader(false));
          }
        }
      }
    });
  };
  const uploadDataToDtx = async (i, tenantToken) => {
    let variables = {};
    Excel.run(async function (context) {
      console.log("hjdbgdhbghbg");
      const sheet = context.workbook.worksheets.getActiveWorksheet(); // String(MMmappingData?.custom_material_id?.address + i)
      let custom_material_id = MMmappingData?.custom_material_id?.address
        ? sheet.getRange(String(MMmappingData?.custom_material_id?.address + i)).load("values")
        : null;
      let erp_material_id = MMmappingData?.erp_material_id?.address
        ? sheet.getRange(String(MMmappingData?.erp_material_id?.address + i)).load("values")
        : null;
      let gross_weight = MMmappingData?.gross_weight?.address
        ? sheet.getRange(String(MMmappingData?.gross_weight?.address + i)).load("values")
        : null;
      let inventory = MMmappingData?.inventory?.address
        ? sheet.getRange(String(MMmappingData?.inventory?.address + i)).load("values")
        : null;
      let lead_time = MMmappingData?.lead_time?.address
        ? sheet.getRange(String(MMmappingData?.lead_time?.address + i)).load("values")
        : null;
      let material_description = MMmappingData?.material_description?.address
        ? sheet.getRange(String(MMmappingData?.material_description?.address + i)).load("values")
        : null;
      let material_group = MMmappingData?.material_group?.address
        ? sheet.getRange(String(MMmappingData?.material_group?.address + i)).load("values")
        : null;
      let material_name = MMmappingData?.material_name?.address
        ? sheet.getRange(String(MMmappingData?.material_name?.address + i)).load("values")
        : null;
      let others = MMmappingData?.others?.address
        ? sheet.getRange(String(MMmappingData?.others?.address + i)).load("values")
        : null;
      let parent_id = MMmappingData?.parent_id?.address
        ? sheet.getRange(String(MMmappingData?.parent_id?.address + i)).load("values")
        : null;
      let plm_material_id = MMmappingData?.plm_material_id?.address
        ? sheet.getRange(String(MMmappingData?.plm_material_id?.address + i)).load("values")
        : null;
      let plm_record_id = MMmappingData?.plm_record_id?.address
        ? sheet.getRange(String(MMmappingData?.plm_record_id?.address + i)).load("values")
        : null;
      let product_code = MMmappingData?.product_code?.address
        ? sheet.getRange(String(MMmappingData?.product_code?.address + i)).load("values")
        : null;
      let quantity = MMmappingData?.quantity?.address
        ? sheet.getRange(String(MMmappingData?.quantity?.address + i)).load("values")
        : null;
      let unit_cost = MMmappingData?.unit_cost?.address
        ? sheet.getRange(String(MMmappingData?.unit_cost?.address + i)).load("values")
        : null;
      let storage_loc = MMmappingData?.storage_loc?.address
        ? sheet.getRange(String(MMmappingData?.storage_loc?.address + i)).load("values")
        : null;
      let unit_of_material = MMmappingData?.unit_of_material?.address
        ? sheet.getRange(String(MMmappingData?.unit_of_material?.address + i)).load("values")
        : null;
      let unit_sale_price = MMmappingData?.unit_sale_price?.address
        ? sheet.getRange(String(MMmappingData?.unit_sale_price?.address + i)).load("values")
        : null;
      let weight_unit = MMmappingData?.weight_unit?.address
        ? sheet.getRange(String(MMmappingData?.weight_unit?.address + i)).load("values")
        : null;
      await context.sync();
      variables = {
        custom_material_id: custom_material_id?.values[0][0],
        erp_material_id: erp_material_id?.values[0][0],
        gross_weight: gross_weight?.values[0][0],
        inventory: inventory?.values[0][0],
        lead_time: lead_time?.values[0][0],
        material_description: material_description?.values[0][0],
        material_group: material_group?.values[0][0],
        material_name: material_name?.values[0][0],
        others: others?.values[0][0],
        parent_id: parent_id?.values[0][0],
        plm_material_id: plm_material_id?.values[0][0],
        plm_record_id: plm_record_id?.values[0][0],
        product_code: product_code?.values[0][0],
        quantity: quantity?.values[0][0],
        unit_cost: unit_cost?.values[0][0],
        storage_loc: storage_loc?.values[0][0],
        unit_of_material: unit_of_material?.values[0][0],
        unit_sale_price: unit_sale_price?.values[0][0],
        weight_unit: weight_unit?.values[0][0],
      };
      console.log(variables, "variables");
      const body = {
        operationName: "CreateMaterialMaster",
        variables: variables,
        query:
          "mutation CreateMaterialMaster($custom_material_id: String, $erp_material_id: String, $gross_weight: Int, $inventory: Int, $lead_time: Int, $material_description: String, $material_group: Int, $material_name: String, $others: jsonb, $parent_id: Int, $plm_material_id: String, $plm_record_id: String, $product_code: Int, $quantity: Int, $storage_loc: Int, $unit_cost: Int, $unit_of_material: String, $unit_sale_price: Int, $weight_unit: String) {\n  insert_core_material_master_one(\n    object: {custom_material_id: $custom_material_id, erp_material_id: $erp_material_id, gross_weight: $gross_weight, inventory: $inventory, lead_time: $lead_time, material_description: $material_description, material_group: $material_group, material_name: $material_name, others: $others, parent_id: $parent_id, plm_material_id: $plm_material_id, plm_record_id: $plm_record_id, product_code: $product_code, quantity: $quantity, unit_cost: $unit_cost, storage_loc: $storage_loc, unit_of_material: $unit_of_material, unit_sale_price: $unit_sale_price, weight_unit: $weight_unit}\n  ) {\n    id\n    __typename\n  }\n}\n",
      };

      fetch("https://beacon-dev.hasura.app/v1/graphql", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + tenantToken,
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((data) => console.log(data))
        .catch((error) => console.error(error));
    });
  };

  async function handleRemove() {
    await Excel.run(async (context) => {
      const range = context.workbook.getSelectedRange();
      range.load("address");
      await context.sync();
      let rangeAddress = range.address.split("!");
      let selectedAddress = String(rangeAddress[1]).substring(0, 1);
      Object.entries(MMmappingData).map(([key, value]) => {
        if (value?.address === selectedAddress) {
          const copy = { ...MMmappingData };
          delete copy[key];
          dispatch(setMMmappingData2(copy));
          context.workbook.worksheets.getActiveWorksheet().getRange(String(selectedAddress + ":" + selectedAddress))
            .format.fill.clear;
          return context.sync();
        }
      });
    });
  }

  return (
    <div>
      <body className="ms-font-m ms-welcome ms-Fabric">
        <BeaconHeader />
        <div id="app-body" className="app-body">
          <p>
            Template Name : <b>{typeOfData.label}</b>
          </p>
          <div className="select-map-feild">
            <p htmlFor="">Selelct the type of region</p>
            <Select options={field} styles={customStyles} onChange={selectRangeType} value={rangeType} />
            <p htmlFor="">Selelct the field</p>
            <Select options={fieldFor} styles={customStyles} onChange={selectRangeFor} value={rangeFor} />

            <div className="action">
              <p className="warning" id="warning">
                {" "}
              </p>
            </div>
            {intersection && <h5>{rangeType.label} is already reserved</h5>}
            <div className="action">
              <button className="apply" id="run" onClick={reserveSelectedSpace}>
                Apply
              </button>
              <button className="clear" id="clear" onClick={handleRemove}>
                Clear
              </button>
            </div>

            <table id="configure-table">
              <thead>
                <tr>
                  <th>Mapping to </th>
                  <th>Address </th>
                </tr>
              </thead>
              <tbody>
                {Object.entries(MMmappingData)?.map(
                  ([key, value]) =>
                    value?.address && (
                      <tr>
                        <td>{value.name}</td>
                        <td>{value.address}</td>
                      </tr>
                    )
                )}
              </tbody>
            </table>
            {mustHaveField && (
              <p className="warnings" id="warnings">
                You have to map all starred regions to complete the data export
              </p>
            )}
            <div className="upload">
              {!uploadLoader ? (
                <button
                  onClick={() => {
                    uploadData();
                  }}
                >
                  Upload data{""}
                </button>
              ) : (
                <button>
                  <BeatLoader
                    color="#000000"
                    size={5}
                    onClick={() => {
                      uploadData();
                    }}
                  />
                </button>
              )}
            </div>
          </div>
        </div>
      </body>
    </div>
  );
}

export default MappingSelectionPage;
