import React, { useEffect, useState } from "react";
import BeaconHeader from "../../commonComponents/BeaconHeader/BeaconHeader";
import "./FormSelectionPage.css";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { setNextPage, setTypeOfMapping, setTypeOfData } from "../../redux-toolkit/slice/TaskpaneSlice";

function FormSelectionPage() {
  const { typeOfMapping } = useSelector((state) => state.taskpane);
  const { typeOfData } = useSelector((state) => state.taskpane);
  const [error, setError] = useState(false);
  const dispatch = useDispatch();
  const actions = [
    { label: "Form data ", value: 1 },
    { label: "Table ", value: 2 },
  ];
  const actions2 = [
    { label: "material master ", value: 1 },
    { label: "Table ", value: 2 },
  ];
  const handleChange = (typeOfMapping) => {
    setError(false);
    dispatch(setTypeOfMapping(typeOfMapping));
  };
  const handleChange2 = (typeOfData) => {
    setError(false);
    dispatch(setTypeOfData(typeOfData));
  };
  const changePage = () => {
    if (typeOfMapping && typeOfData) {
      dispatch(setNextPage("mappingPage"));
    } else {
      setError(true);
      setTimeout(() => {
        setError(false);
      }, 3000);
    }
  };

  useEffect(() => {}, [typeOfData]);

  const customStyles = {
    control: (base, state) => ({
      ...base,
      borderColor: state.isFocused ? "#383332" : "##383332",
      boxShadow: state.isFocused ? null : null,
      "&:hover": { borderColor: state.isFocused ? null : null },
      width: 250,
      height: 30,
    }),
    option: (base, state) => ({
      ...base,
      backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
      color: state.isSelected ? "#383332" : "#383332",
      "&:hover": { backgroundColor: "#F8F4F0" },
    }),
  };
  return (
    <div>
      <body className="ms-font-m ms-welcome ms-Fabric">
        <BeaconHeader />

        <main id="app-body" className="app-body">
          <div id="app-body" className="app-body">
            <div className="select-map-feild">
              <p htmlFor="">Selelct the type of mapping</p>
              <Select options={actions} styles={customStyles} onChange={handleChange} value={typeOfMapping} />
              <p htmlFor="">Selelct data mapping name</p>
              <Select options={actions2} styles={customStyles} onChange={handleChange2} value={typeOfData} />
              <div className="formSelectionButton">
                <button className="nextPage" onClick={changePage}>
                  forward
                </button>
                
              </div>
           { error &&  <p className="optionWarning"> Please select all the options </p>}
            </div>
          </div>
        </main>
      </body>
    </div>
  );
}

export default FormSelectionPage;
