import React from "react";
import "./BeaconHeader.css";
import { HOSTED_URL } from "../../../config";

function BeaconHeader() {
  return (
    <header className="ms-welcome__header">
      <img src={`${HOSTED_URL}/assets/beacon-without-background -logo.png`} alt="Beacon-India" title="Beacon-India" />
      <p>Beacon-DTX mapping configuration</p>
    </header>
  );
}

export default BeaconHeader;
